const displayNum = 3;

function bolding(text, bolding) {
  const words = text.split(bolding);
  return (
    <span>{words[0]}<b>{bolding}</b>{words[1]}</span>
  );
}

function createEnglishRows(classes, rows, num) {
  return (
    rows.slice(0, num).map((row) => (
      <li className="listItem"><p className={classes.normalText}>{bolding(row.content, "Hiroki Yoshikawa")} doi:<a href={row.url} target="_blank" rel="noopener noreferrer">{row.url}</a></p></li>
    ))
  );
}
function createJapaneseRows(classes, rows, num) {
  return (
    rows.slice(0, num).map((row) => (
      <li className="listItem"><p className={classes.normalText}>{bolding(row.content, "吉川 寛樹")}</p></li>
    ))
  );
}
function createAward(classes, rows) {
  return (
    rows.map((row) => (
      <li className="listItem"><p className={classes.normalText}>{bolding(row.content, row.award)}</p></li>
    ))
  );
}

class ShowMoreComponent extends React.Component{
  constructor(props) {
    super(props);
    const _more = props.rows.length - displayNum;
    this.state = {rowNum: displayNum, text: "show more " + _more , moreNum: _more, rows: props.rows, language: props.language};
    
  }

  clickAction(event){
      if ( this.state.rowNum < this.state.rows.length ) {
        this.setState({rowNum: this.state.rows.length, text: "show latest 3"});
      }else {
        this.setState({rowNum: displayNum, text: "show more " + this.state.moreNum});
      }
  }

  render(){
    console.log(this.props.language)
    if (this.props.language === "ja" && this.state.rows.length > displayNum){
      return (
        <TableCell classesNames={this.props.classesContents.muiCells}>
          <ul>
            {createJapaneseRows(this.props.classesContents, this.state.rows, Math.min(this.state.rowNum, this.state.rows.length))}
            <div onClick={(event) => this.clickAction(event) }>
              <p className={this.props.classesContents.showMoreText}>{this.state.text}</p>
            </div>
          </ul>
        </TableCell>
      )
    }else if (this.props.language === "en" && this.state.rows.length > displayNum) {
      return (
        <TableCell classesNames={this.props.classesContents.muiCells}>
          <ul>
            {createEnglishRows(this.props.classesContents, this.state.rows, Math.min(this.state.rowNum, this.state.rows.length))}
            <div onClick={(event) => this.clickAction(event) }>
              <p className={this.props.classesContents.showMoreText}>{this.state.text}</p>
            </div>
          </ul>
        </TableCell>
      )
    }else if (this.props.language === "ja"){
      return (
        <TableCell classesNames={this.props.classesContents.muiCells}>
          <ul>
            {createJapaneseRows(this.props.classesContents, this.state.rows, Math.min(this.state.rowNum, this.state.rows.length))}
          </ul>
        </TableCell>
      )
    }else {
      return (
        <TableCell classesNames={this.props.classesContents.muiCells}>
          <ul>
            {createEnglishRows(this.props.classesContents, this.state.rows, Math.min(this.state.rowNum, this.state.rows.length))}
          </ul>
        </TableCell>
      )
    }
  }
}

